*{
  color:white;
  background: transparent;
  font-size:1vw;
}

.main-container{
  margin:0;
  padding:0;
  display:grid;
  grid-template-columns: 20% 80%;
  background-image: url('./images/blockchain.png');
  background-repeat: no-repeat;
  /* background-color: black; */
  background-blend-mode: luminosity;
  background-size: cover;

}

.main-container>h1{
  text-align: center;
  grid-column: 1/3;
  -webkit-box-shadow: 1px 1px 2px 1px;
          box-shadow: 1px 1px 2px 1px;
  margin:0;
  text-shadow: 2px 2px 7px black;
  font-size: 3em;
}

.side-bar{
  grid-row: 2;
  border-right: 1px solid red;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.search-panel{
  width: 15vw;
  -ms-flex-item-align: center;
      align-self: center;
  margin-top: 3vh;
}

.search-bar{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#input{
  background: -webkit-gradient(linear, left top, left bottom, from(#131313de), to(#808080a1));
  background: -webkit-linear-gradient(#131313de, #808080a1);
  background: -o-linear-gradient(#131313de, #808080a1);
  background: linear-gradient(#131313de, #808080a1);
  margin: 1vw 0vw 1vw 1vw;
  height: 1.4vw;  
  width: 13vw;
}

.fa-search{
  position: relative;
  float: right;
  bottom: 4vh;
  right: 0.5vw;
}

button{
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  margin: 0.5vw 001vw;
  height: 2.5vw;
  background: -webkit-gradient(linear, left top, right top, from(#8080801c), to(#0409ab8c));
  background: -webkit-linear-gradient(left, #8080801c, #0409ab8c);
  background: -o-linear-gradient(left, #8080801c, #0409ab8c);
  background: linear-gradient(to right, #8080801c, #0409ab8c);
  text-shadow: 2px 2px 7px #d45c5c;
  font-weight: 800;
  border: none;
  -webkit-transition: background 3000ms;
  -o-transition: background 3000ms;
  transition: background 3000ms;
}

button:hover{
  cursor: pointer;
  background: -webkit-gradient(linear, left top, right top, from(#a90505), to(#8080801c));
  background: -webkit-linear-gradient(left, #a90505, #8080801c);
  background: -o-linear-gradient(left, #a90505, #8080801c);
  background: linear-gradient(to right, #a90505, #8080801c);
}

.card-info{
  display:none;
  width: 17vw;
  margin: 2vw;
}

#logo-one{
  width: 3em;
}

.yellow{
  color:yellow;
  text-shadow: 2px 2px 2px black;
}

.disp-header{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}   

h2{
  margin: 1vw 1vw 1vw 5vw;  
}

.fa-sort-amount-up{
  margin:1vw;
  -ms-flex-item-align: center;
      align-self: center;
  color: #5fec0c;
  display:none;
  text-shadow: 2px 2px 7px black;
}

.fa-sort-amount-down{
  margin:1vw;
  -ms-flex-item-align: center;
      align-self: center;
  color: #5fec0c;
  display:none;
  text-shadow: 2px 2px 7px black;
}

.true{
  display: block;
}

.false{
  display: none;
}

ul{
  height: 80vh;
  list-style: none;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  overflow: scroll;
  overflow-x: hidden;
  margin-top: 0;
}

ul::-webkit-scrollbar{

}

.cards{
  width:10vw;
  height:10vw;
  background:-webkit-gradient(linear, left top, left bottom, from(#4465ea82), to(#001233a8));
  background:-webkit-linear-gradient(#4465ea82, #001233a8);
  background:-o-linear-gradient(#4465ea82, #001233a8);
  background:linear-gradient(#4465ea82, #001233a8);
  margin:2vw;
  text-align: center;
  -webkit-box-shadow: 1px 1px 20px 5px;
          box-shadow: 1px 1px 20px 5px;
  border-radius: 5%;
  border-bottom-right-radius: 30%;
}

.cards:hover{
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

li>img{
margin:0.5vw;
}

li>h3{
  color: #d4ea82;
    text-shadow: 2px 2px 7px black;
    margin: 0;
}
li>p{
  margin:0;
}

hr{
  -webkit-border-image:
      -webkit-gradient(linear, left bottom, left top, from(black), to(rgba(0, 0, 0, 0))) 1 100%;
  -webkit-border-image:
      -webkit-linear-gradient(bottom, black, rgba(0, 0, 0, 0)) 1 100%;
       -o-border-image:
      -o-linear-gradient(bottom, black, rgba(0, 0, 0, 0)) 1 100%;
          border-image:
      -webkit-gradient(linear, left bottom, left top, from(black), to(rgba(0, 0, 0, 0))) 1 100%;
          border-image:
      linear-gradient(to top, black, rgba(0, 0, 0, 0)) 1 100%;
}


